
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios';


import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';

export default {
name:'create',
data:function() {
return{
title: '',
content: '',

toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
undo: true, // 
redo: true, // 
trash: true, // 
save: true, // eventssave
navigation: true, // 
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
preview: true, // 
}
}
},
components: {
Group,
XTextarea,
XButton,
mavonEditor
},
methods :{
uploadImg:function(pos,file){
var app = this;


var params = new FormData();
params.append('smfile', file)

axios.post('https://sm.ms/api/upload', params, {



headers:{ 'Content-Type': 'multipart/form-data' }
}).then(function(response){

app.$refs.md.$img2Url(pos, response.data.data.url);
});
},
addtopic: function(){
var app = this;
this.$http.post('/topics',{
title: this.title,
content: this.content,
tab: 'dev',
accesstoken:this.$store.state.accesstoken
}).then(function(response) {

if (response.data && response.data.success) {

app.$router.replace('/topiclist/dev')
}
})
}
}
}



